import { useState } from 'react';
import './plus.css';
import Time from './../time/time';


export default function PlusBtn({ onHighlight }) {
    const [showTime, setShowTime] = useState(false);
    const [subject, setSubject] = useState('');
    const [times, setTimes] = useState([]);

    const handleSubjectClick = (subject, times) => {
        setSubject(subject);
        setTimes(times);
        setShowTime(true);
    };


    return (
        <div className='plus-container'>
            <button onClick={() => handleSubjectClick('', [' - 3~5', ' - 1~3', ' - 2~4'])}>
                <h3><nav className='required'></nav> -  ()</h3>
            </button>
            <button onClick={() => handleSubjectClick('', [' - 1~3', ' - 9~11', ' - 9~11'])}>
                <h3><nav className='required'></nav> -  ()</h3>
            </button>
            <br />
            <button onClick={() => handleSubjectClick('', [' - 2~3', ' - 4~5', ' - 10~11'])}>
                <h3><nav className='elective'></nav> -  ()</h3>
            </button>
            <button onClick={() => handleSubjectClick('', [' - 11~12', ' - 1~2', ' - 9~10'])}>
                <h3><nav className='elective'></nav> -  ()</h3>
            </button>
            <button onClick={() => handleSubjectClick('', [' - 4~6', ' - 6~8', ' - 4~6'])}>
                <h3><nav className='elective'></nav> -  ()</h3>
            </button>
            {showTime && <Time onHighlight={onHighlight} subject={subject} times={times} />}
        </div>
    );
}
